const ADD_MESSAGE = 'samurai-network/messages/ADD-MESSAGE';


let initialState = {
    messages: [
        { id: 1, message: 'Hi' },
        { id: 2, message: 'How r U?' },
        { id: 3, message: 'Everything is Fine!' }
    ],
    dialogs: [
        { id: 1, name: 'Andrey' },
        { id: 2, name: 'Serega' },
        { id: 3, name: 'Maxim' },
        { id: 4, name: 'Oleg' },
        { id: 5, name: 'Vova' },
        { id: 6, name: 'Artur' },
        { id: 7, name: 'Pasha' },
    ]
}

export const messagesReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_MESSAGE:
            return {
                ...state,
                messages: [...state.messages, { id: 4, message: action.newMessageBody }],
            }
        default:
            return state;
    }
};

export const addMessageCreator = (newMessageBody) => ({ type: ADD_MESSAGE, newMessageBody });

export default messagesReducer;