import React from 'react';
import { Form, Field } from 'react-final-form';
import { connect } from 'react-redux';
import { Redirect } from 'react-router-dom';
import { login } from '../../redux/auth-reducer';
import { composeValidators, required } from '../../utils/validators/validators';
import { Input } from '../common/Forms/formsControl';
import style from './../common/Forms/formsControls.module.css'

const LoginForm = ({onSubmit, validate, captchaUrl, error}) => (
        <Form
            onSubmit={onSubmit}
            validate={validate}
            render={({ submitError,
                handleSubmit,
                form,
                submitting,
                pristine,
                values}) => (
                <form onSubmit={handleSubmit} >
                    <h2>Enter your Email</h2>
                    <div>
                        <label>Email</label>
                <Field name="email" 
                component={Input}
                validate={validate} 
                placeholder="Email"/>
            </div>
            <h2>Enter the Password</h2>
        <div>
          <Field name="password" component={Input} placeholder="password"
           validate={validate} type="password"/>
        </div>
        <div>
            <Field name="rememberMe" component={Input} type="checkbox" />
            <label>remember me</label>
        </div>
        <div>
            {error && <div className={style.error}> {error}</div>}
        </div>

        <div>
            {captchaUrl && <img src={captchaUrl}/>}
            {captchaUrl && <Field name="captcha" component={Input} validate={validate}  type="text" />}
        </div>
            <div>
                <button type="submit" disabled={submitting || pristine} errors={submitError}>Log In</button>
            </div>
                </form>
            )}
        />
    )

 



const Login = ({login, isAuth, error, captchaUrl}) => {

    const onSubmit = formData => {
        login(formData.email, formData.password, formData.rememberMe, formData.captcha, error)
    }

    if(isAuth){
        return <Redirect to={"/profile"} />
    }

    return <div>
        <h1>Login</h1>
        <LoginForm onSubmit={onSubmit} validate={composeValidators(required)} submitError={error} captchaUrl={captchaUrl } />
    </div>
}

const mapStateToProps = (state) => ({
    captchaUrl: state.auth.captchaUrl,
    isAuth: state.auth.isAuth,
    error: state.auth.error
})

export default connect( mapStateToProps, {login} )(Login);