import React from 'react';
import { connect } from 'react-redux';
import { toggleFollow, setCurrentPage, requestUsers, toggleFollowingInProgress } from '../../redux/users-reducer';
import Users from './Users';
import Preloader from '../common/Preloader/Preloader';
import { getUsers, getTotalUsersCount, getPageSize, getCurrentPage, getIsFetching, getFollowingInProgress, getPortionSize } from '../../redux/users-selectors';




class UsersContainer extends React.Component {
    componentDidMount() {
        const {currentPage, pageSize} = this.props
        this.props.requestUsers(currentPage, pageSize);
    }

    onPageChanged = (pageNumber) => {
        const pageSize = this.props
        this.props.setCurrentPage(pageNumber);
        this.props.requestUsers(pageNumber, pageSize)
    }

    render() {

        return <>
            {this.props.isFetching ? <Preloader /> : null}
            <Users totalUsersCount={this.props.totalUsersCount}
                pageSize={this.props.pageSize}
                currentPage={this.props.currentPage}
                onPageChanged={this.onPageChanged}
                users={this.props.users}
                toggleFollow={this.props.toggleFollow}
                followingInProgress={this.props.followingInProgress} 
                portionSize={this.props.portionSize} />
        </>
    }
}

let mapStateToProps = (state) => {
    
    return {
        users: getUsers(state),
        pageSize: getPageSize (state),
        totalUsersCount: getTotalUsersCount (state),
        currentPage: getCurrentPage(state),
        isFetching: getIsFetching(state),
        followingInProgress: getFollowingInProgress(state),
        portionSize: getPortionSize(state)
    }
}

export default connect(mapStateToProps, 
    { toggleFollow, setCurrentPage, toggleFollowingInProgress, requestUsers }
    )(UsersContainer);