export const required = value => 
     value ? undefined : "Field is required"


export const maxLengthCreator = maxLength => value => {
    if(!value) return undefined;
    if (value.length > maxLength) return `Max length is ${maxLength} symbols`;
    return undefined
}

export const linkValidator = (values, linkField) => {
    if(values){
    if (linkField.active) {
        const linkName = linkField.name.replace('contacts.', '')
        if ((linkField.value.includes(`${linkName}.com/`) && linkField.value.indexOf(`${linkName}`) === 0 ) || (linkField.value.includes(`https://${linkName}.com/`) && linkField.value.indexOf("https://") === 0)) {
                return undefined
            } else { return `We have some troubles with your link - ${linkName}. Please note: We expect ${linkName}.com/ or https://${linkName}.com/` }
    }
}
    return undefined
}

export const composeValidators = (...validators) => (values, nameValueObj, fieldObj) => {
    return validators.reduce((error, validator) => error || validator(values, fieldObj), undefined)
  }