import * as axios from "axios";

const instance = axios.create({
    withCredentials: true,
    baseURL: 'https://social-network.samuraijs.com/api/1.0/',
    headers: {
        'API-KEY': '8389851f-540e-4430-b9f2-c897b55257fa'
    }
});

export const usersAPI = {
    async getUsers(currentPage, pageSize) {
        let response = await instance.get(`users?page=${currentPage}&count=${pageSize}`)
        return response.data;
    },

    async setFollow(user) {
        if (user.followed === false) {
            let response = await instance.post(`follow/${user.id}`, {})
            return response.data
        } else {
            let response = await instance.delete(`follow/${user.id}`, {})
            return response.data
        }
    },

    getProfile(userId) {
        console.warn('Obsolete method. Please use profileAPI object.')
        return profileAPI.getProfile(userId)
    }
};

export const profileAPI = {
    async getProfile(userId) {
        let response = await instance.get(`profile/${userId}`)
        return response.data
    },

    async getStatus(userId) {
        let response = await instance.get(`profile/status/${userId}`)
        return response.data
    },

    async updateStatus(status) {
        let response = await instance.put('profile/status', { status: status })
        return response.data
    },

    async savePhoto(photoFile) {
        const formData = new FormData();
        formData.append("image", photoFile)
        let response = await instance.put('profile/photo', formData, {
            headers: {
                'Content-Type': 'multipart/form-data'
            }
        })
        return response.data
    },

    async saveProfile(profile){
        let response = await instance.put('profile', profile)
        return response.data
    }
}

export const authAPI = {

    async me() {
        const response = await instance.get(`auth/me`, {})
        return response.data
    },
    async login(email, password, rememberMe = false, captcha = null) {
        const response = await instance.post(`auth/login`, { email, password, rememberMe, captcha })
        return response.data
    },
    async logout() {
        const response = await instance.delete(`auth/login`, {})
        return response.data;
    }

};

export const securityAPI = {
    getCaptchaUrl(){
        return instance.get(`security/get-captcha-url`)
    }
}