import { authAPI, securityAPI } from "../api/api";

const SET_USER_DATA = 'samurai-network/auth/SET-USER-DATA';
const GET_CAPTCHA_URL_SUCCESS = 'samurai-network/auth/GET-CAPTCHA-URL-SUCCESS'


let initialState =  {
    userId: null,
    email: null,
    login: null,
    isFetching: false,
    isAuth: false,
    error: "",
    captchaUrl: null //Captcha is not required, if null
};

const authReducer = (state = initialState, action) => {

    switch(action.type){
        case SET_USER_DATA:
        case GET_CAPTCHA_URL_SUCCESS:
            return {  
                ...state,
                ...action.payload,
                }
        
        default:
            return state; 
    }
}

export const setAuthUserData = (userId, email, login, isAuth, error) => (
    { type: SET_USER_DATA, payload: { userId, email, login, isAuth, error } 
});

export const getCaptchaUrlSuccess = (captchaUrl) => (
    { type: GET_CAPTCHA_URL_SUCCESS, payload: { captchaUrl } 
});

export const getAuthUserData = () => async dispatch => {
    const response = await authAPI.me()
        if(response.resultCode === 0){
            const {id, login, email} = response.data
            dispatch(setAuthUserData(id, email, login, true, ""))
        }
}
export const login =  (email, password, rememberMe, captcha) => async (dispatch) => {
    const response = await authAPI.login(email, password, rememberMe, captcha)
            if (response.resultCode === 0) {
                dispatch(getAuthUserData());
            } else {
                if(response.resultCode === 10){
                    dispatch(getCaptchaUrl())
                }
                const message = response.messages.length > 0 ? response.messages[0] : "Some error";
                dispatch(setAuthUserData(null, null, null, false, message));
            }
}

export const getCaptchaUrl =  () => async (dispatch) => {
    const response = await securityAPI.getCaptchaUrl();
    const captchaUrl = response.data.url;
    dispatch(getCaptchaUrlSuccess(captchaUrl)) 
}

export const logout = () => async (dispatch) => {
   const response = await authAPI.logout()
        if(response.resultCode === 0){
            dispatch(setAuthUserData(null, null, null, false, ""))
        }
}
export default authReducer;