import { profileAPI, usersAPI } from "../api/api";

const ADD_POST = 'samurai-network/profile/ADD-POST';
const SET_USER_PROFILE = 'samurai-network/profile/SET-USER-PROFILE';
const SET_STATUS = 'samurai-network/profile/SET-STATUS';
const DELETE_POST = 'samurai-network/profile/DELETE-POST';
const SAVE_PHOTO_SUCCESS = 'samurai-network/profile/SAVE-PHOTO-SUCCESS';
const SAVE_PROFILE = 'samurai-network/profile/SAVE-PROFILE';

let initialState = {
    postsData: [
        { id: 1, message: 'Hi, how are U?', "likesCount": '10' },
        { id: 2, message: 'It\'s my first post', "likesCount": '15' }
    ],
    profile: null,
    status: '',
    editMode: false
}

const profileReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_POST:
            let newPost = {
                id: 3,
                message: action.myNewPost,
                likesCount: '0'
            };
            return {
                ...state,
                postsData: [...state.postsData, newPost],
                newPostText: ''
            }
        case SET_USER_PROFILE:
            return {
                ...state,
                profile: action.profile
            }
        case SET_STATUS:
            return {
                ...state,
                status: action.status
            }
        case DELETE_POST:
            return {
                ...state,
                postsData: state.postsData.filter(p => p.id !== action.postId)
            }
        case SAVE_PHOTO_SUCCESS:
            return {
                ...state,
                profile: { ...state.profile, photos: action.photos }
            }
       /*  case SAVE_PROFILE:
            return {
                ...state,
                profile: {...state.profile, profile: action.profile}
            } */
        default:
            return state;
    }
}; 

export const addPostCreator = (myNewPost) => ({ type: ADD_POST, myNewPost });

export const setUserProfile = (profile) => ({ type: SET_USER_PROFILE, profile })

export const setStatus = (status) => ({ type: SET_STATUS, status })

export const deletePost = (postId) => ({ type: DELETE_POST, postId })

export const savePhotoSuccess = (photos) => ({ type: SAVE_PHOTO_SUCCESS, photos })

export const saveProfileSuccess = (profile) => ({ type: SAVE_PROFILE, profile })

export const getProfile = (userId) =>
    async (dispatch) => {
        const response = await usersAPI.getProfile(userId)
        dispatch(setUserProfile(response));
    }

export const getStatus = (userId) =>
    async (dispatch) => {
        const response = await profileAPI.getStatus(userId)
        dispatch(setStatus(response));
    }

export const updateStatus = (status) =>
    async (dispatch) => {
        const response = await profileAPI.updateStatus(status)
        if (response.resultCode === 0) {
            dispatch(setStatus(status));
        }
    }

export const savePhoto = (file) =>
    async (dispatch) => {
        const response = await profileAPI.savePhoto(file)
        if (response.resultCode === 0) {
            dispatch(savePhotoSuccess(response.data.photos));
        }
    }

    export const saveProfile = (profile) => async (dispatch, getState) => {
        const userId = getState().auth.userId
        const response = await profileAPI.saveProfile(profile)
        if (response.resultCode === 0) {
            dispatch(getProfile(userId));
        }
    }

export default profileReducer;