import { usersAPI } from "../api/api";
import { updateObjInArray } from "../utils/object-helpers";

const TOGGLE_FOLLOW_UNFOLLOW = 'samurai-network/users/TOGGLE-FOLLOW-UNFOLLOW'
const SET_USERS = 'samurai-network/users/SET-USERS';
const SET_CURRENT_PAGE = 'samurai-network/users/SET-CURRENT-PAGE';
const SET_TOTAL_USERS_COUNT = 'samurai-network/users/SET-TOTAL-USERS-COUNT';
const TOGGLE_IS_FETCHING = 'samurai-network/users/TOGGLE-IS-FETCHING';
const TOGGLE_FOLLOWING_IN_PROGRESS = 'samurai-network/users/TOGGLE-FOLLOWING-IN-PROGRESS';


let initialState =  {
    users: [],
    pageSize: 5,
    totalUsersCount: 0,
    currentPage: 1,
    isFetching: true, 
    followingInProgress: [],
    portionSize: 10
};

const usersReducer = (state = initialState, action) => {

    switch(action.type){
        case TOGGLE_FOLLOW_UNFOLLOW:
            return { 
                ...state,
                users: updateObjInArray(state.users, action.userId, "id")
            } 

            case SET_USERS: {
                return { ...state, users: action.users}
            }

            case SET_CURRENT_PAGE: {
                return {...state, currentPage: action.currentPage}
            }

            case SET_TOTAL_USERS_COUNT: {
                return { ...state, totalUsersCount: action.count}
            }

            case TOGGLE_IS_FETCHING: {
                return { ...state, isFetching: action.isFetching}
            }

            case TOGGLE_FOLLOWING_IN_PROGRESS: {
                return { ...state,
                    followingInProgress: action.followingInProgress
                    ? [...state.followingInProgress, action.userId] 
                    : [...state.followingInProgress.filter(id => id !== action.userId)]}
            }

        default:
            return state;
    }
}

export const toggleFollowUnfollow = (userId) => ( {type: TOGGLE_FOLLOW_UNFOLLOW, userId})
export const setUsers = (users) => ({type: SET_USERS, users})
export const setCurrentPage = (currentPage) => ({type: SET_CURRENT_PAGE, currentPage})
export const setTotalUsersCount = (totalUsersCount) => ({type: SET_TOTAL_USERS_COUNT, count: totalUsersCount})
export const toggleIsFetching = (isFetching) => ({type: TOGGLE_IS_FETCHING, isFetching})
export const toggleFollowingInProgress = (followingInProgress, userId) => ({type: TOGGLE_FOLLOWING_IN_PROGRESS, followingInProgress, userId})

export const requestUsers = (page, pageSize) =>
    async (dispatch) => {
        dispatch(toggleIsFetching(true));
        let data = await usersAPI.getUsers(page, pageSize)
        
            dispatch(toggleIsFetching(false));
            dispatch(setUsers(data.items));
            dispatch(setTotalUsersCount(data.totalCount));
    };

export const toggleFollow = (user) =>
    async (dispatch) => {
        toggleFollowingInProgress(true, user.id)
        let data = await usersAPI.setFollow(user)
              if (data.resultCode === 0){
                dispatch(toggleFollowUnfollow(user.id))  
              }
                dispatch(toggleFollowingInProgress(false, user.id))

    }

export default usersReducer;